import axios from 'axios'
export default {
  name: 'taskDetail',
  data () {
    return {
      level: parseInt(localStorage.getItem('level')),
      mission: {
        name: '',
        moduleID: parseInt(sessionStorage.getItem('taskID')),
        startTime: '',
        endTime: '',
        ownerID: '',
        content: '',
        target: ''
      },
      task: '',
      missionList: [{
        name: '',
        moduleID: parseInt(sessionStorage.getItem('taskID')),
        startTime: '',
        endTime: '',
        ownerID: '',
        content: '',
        target: ''
      }],
      studentList: [],
      addMission: false
    }
  },
  methods: {
    goProjectDetail (projectID, projectName) {
      sessionStorage.setItem('pid', projectID)
      sessionStorage.setItem('pname', projectName)
      this.$router.push('/projectDetail')
    },
    handleAddMission () {
      this.mission.name = ''
      this.mission.startTime = ''
      this.mission.endTime = ''
      this.mission.content = ''
      this.mission.target = ''
      this.mission.ownerID = -1
      this.addMission = true
    },
    getTask () {
      axios.get(this.url + '/module/id/' + sessionStorage.getItem('taskID'))
        .then(this.getTaskSucc)
    },
    getTaskSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.task = res
      }
    },
    getMissionList () {
      axios.get(this.url + '/mission/module/' + sessionStorage.getItem('taskID'))
        .then(this.getMissionListSucc)
    },
    getMissionListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.missionList = res
      } else alert(res.data)
    },
    getStudentList () {
      // axios.get('/static/testData/module.json')
      axios.get(this.url + '/user/level/' + this.student)
        .then(this.getStudentListSucc)
    },
    getStudentListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.studentList = res
      } else alert(res.data)
    },
    submit () {
      if (this.mission.name.length > 10) {
        this.$notify.warning({
          title: '任务名称不能超过十个字！'
        })
        return
      }
      this.$axios({
        method: 'post',
        url: this.url + '/mission/',
        data: this.mission
      }).then((res) => {
        if (res.status === 200) {
          res = res.data
          alert('提交成功！')
          this.missionList.push({
            name: this.mission.name,
            ownerName: res.ownerName
          })
        } else alert(res.data)
      }).catch((err) => {
        alert(err)
      })
      this.addMission = false
    },
    goMissionDetail (missionName, missionID) {
      sessionStorage.setItem('missionName', missionName)
      sessionStorage.setItem('missionID', missionID)
      this.$router.push('/missionDetail')
    }
  },
  mounted () {
    this.mission.ownerID = -1
    this.getTask()
    this.getMissionList()
    this.getStudentList()
  }
}



